import {
  Box,
  Button,
  Container,
  Grid,
  makeStyles,
  Typography,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { MediumTitle } from "../../../shared/Typography";
import {
  getInternalList,
  setAcceptedTransferData,
} from "../../../redux/reducers/internal";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import Reload from "../../../components/Reload";

const AllTransactions = ({ t }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const history = useHistory();

  const { internalList, isLoading } = useSelector(
    ({ internalReducer }) => internalReducer
  );

  const [oldTransfers, setOldTransfers] = useState([]);
  const [newTransfers, setNewTransfers] = useState([]);

  useEffect(() => {
    dispatch(getInternalList());

    dispatch(setAcceptedTransferData(null));
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (Array.isArray(internalList?.data)) {
      const oldTransfers = internalList?.data?.filter((item) => item.status);
      const newTransfers = internalList?.data?.filter((item) => !item.status);

      setOldTransfers(oldTransfers);
      setNewTransfers(newTransfers);
    }
  }, [internalList]);

  return (
    <section>
      {isLoading && <Reload />}
      <Container maxWidth="lg">
        <MediumTitle style={{ marginBottom: 16 }}>
          {t("allFundsReceived")}
        </MediumTitle>
        <Typography variant="h5" className="red">
          {t("newFunds")}
        </Typography>
        <Box mt={"16px"}>
          <Grid container className={classes.grid}>
            <Grid
              item
              xs={4}
              container
              justifyContent="center"
              alignItems="center"
            >
              <Typography variant="h5" className={classes.title}>
                {t("fromWhom")}
              </Typography>
            </Grid>
            <Grid
              item
              xs={4}
              container
              justifyContent="center"
              alignItems="center"
            >
              <Typography variant="h5" className={classes.title}>
                {t("date")}
              </Typography>
            </Grid>
            <Grid
              item
              xs={4}
              container
              justifyContent="center"
              alignItems="center"
            >
              <Typography variant="h5" className={classes.title}>
                {t("amount")}
              </Typography>
            </Grid>
          </Grid>
          {newTransfers?.map((item) => (
            <Box className={classes.boxItem}>
              <Grid container className={classes.box}>
                <Grid
                  item
                  xs={4}
                  container
                  justifyContent="center"
                  alignItems="center"
                >
                  <MediumTitle className={classes.text}>
                    {item.owner}
                  </MediumTitle>
                </Grid>
                <Grid
                  item
                  xs={4}
                  container
                  justifyContent="center"
                  alignItems="center"
                >
                  <MediumTitle className={classes.text}>
                    {item.create_at}
                  </MediumTitle>
                </Grid>
                <Grid
                  item
                  xs={4}
                  container
                  justifyContent="center"
                  alignItems="center"
                >
                  <MediumTitle className={classes.text}>
                    {item.sum_with_commission} {item.currency}
                  </MediumTitle>
                </Grid>
              </Grid>
              <Button
                variant="contained"
                onClick={() => {
                  history.push(`/dashboard/transaction/get/${item.id}`);
                }}
                color="primary"
              >
                {t("acceptFunds")}
              </Button>
            </Box>
          ))}
        </Box>
        <Box mt={"16px"} className={classes.boxWithBd}>
          <Typography variant="h5" style={{ color: "#000" }}>
            {t("oldFunds")}
          </Typography>
          <Grid container className={classes.grid}>
            <Grid
              item
              xs={4}
              container
              justifyContent="center"
              alignItems="center"
            >
              <Typography variant="h5" className={classes.title}>
                {t("fromWhom")}
              </Typography>
            </Grid>
            <Grid
              item
              xs={4}
              container
              justifyContent="center"
              alignItems="center"
            >
              <Typography variant="h5" className={classes.title}>
                {t("date")}
              </Typography>
            </Grid>
            <Grid
              item
              xs={4}
              container
              justifyContent="center"
              alignItems="center"
            >
              <Typography variant="h5" className={classes.title}>
                {t("amount")}
              </Typography>
            </Grid>
          </Grid>
          {oldTransfers?.map((item) => (
            <Box className={classes.boxItem}>
              <Grid container className={classes.box}>
                <Grid
                  item
                  xs={4}
                  container
                  justifyContent="center"
                  alignItems="center"
                >
                  <MediumTitle
                    className={classes.text}
                    style={{ color: "#64748B" }}
                  >
                    {item.owner}
                  </MediumTitle>
                </Grid>
                <Grid
                  item
                  xs={4}
                  container
                  justifyContent="center"
                  alignItems="center"
                >
                  <MediumTitle
                    className={classes.text}
                    style={{ color: "#64748B" }}
                  >
                    {item.create_at}
                  </MediumTitle>
                </Grid>
                <Grid
                  item
                  xs={4}
                  container
                  justifyContent="center"
                  alignItems="center"
                >
                  <MediumTitle
                    className={classes.text}
                    style={{ color: "#64748B" }}
                  >
                    {item.sum_with_commission}
                  </MediumTitle>
                </Grid>
              </Grid>
              <Button variant="contained" color="primary" disabled>
                {t("acceptedFunds")}
              </Button>
            </Box>
          ))}
        </Box>
      </Container>
    </section>
  );
};

export default AllTransactions;

const useStyles = makeStyles((theme) => ({
  title: {
    color: "#64748B",
  },

  boxWithBd: {
    paddingTop: 24,
    marginTop: 24,
    borderTop: "1px solid #E7EAEE",
  },

  boxItem: {
    marginBottom: 8,
    display: "flex",

    "& button": {
      width: 187,
      height: 60,
      marginLeft: 16,
    },

    [theme.breakpoints.down(992)]: {
      flexDirection: "column",

      "& button": {
        width: "100%",
        margin: "16px 0 0 0",
      },
    },
  },

  text: {
    whiteSpace: "nowrap",
    textOverflow: "ellipsis",
    overflow: "hidden",
  },
  grid: {
    marginBottom: 16,
    width: "84%",

    [theme.breakpoints.down(992)]: {
      width: "100%",
    },
  },
  box: {
    background: "#FFF",
    boxShadow: " 0px 1px 0px #E2E8F0",
    borderRadius: 8,
    padding: "16px ",
    height: "60px",

    "&:last-child": {
      marginBottom: 0,
    },
  },
}));
